/* traininfo.css */

.train-info {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.train-info h2 {
  text-align: center;
  margin-bottom: 20px;
}

.train-info .input-container {
  display: flex;
  flex-direction: column; /* Stack input and button vertically */
  align-items: center; /* Center align items */
  margin-bottom: 20px;
}

.train-info .input-container input[type="text"] {
  padding: 8px 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px; /* Added margin bottom for spacing */
}

.train-info .input-container button {
  padding: 8px 12px;
  font-size: 14px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.train-info .train-info-card {
  margin-top: 20px;
}

.train-info .train-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  background-color: #f9f9f9;
}

.train-info .train-card h3 {
  margin-bottom: 10px;
}

.train-info .train-card .card-content {
  line-height: 1.6;
}

.train-info .error-message {
  color: red;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
}
