.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  background-color: #f0f0f0;
}

.header-title {
  margin-bottom: 20px; /* Space between title and navigation links */
}

.nav-links {
  margin-top: auto; /* Pushes nav-links to the bottom */
}

.nav-button {
  display: inline-block;
  padding: 10px 20px;
  margin: 0 10px;
  text-decoration: none;
  color: #fff;
  background-color:red;
  border: 1px solid rebeccapurple;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.nav-button:hover {
  background-color: #ed115f;
  color: #fff;
}

.nav-button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.4); /* Example of focus style */
}
