.leftbar {
  background-color: rgba(41, 41, 41, 0.8); /* Semi-transparent dark gray background */
  backdrop-filter: blur(10px); /* Adjust blur radius as needed */
  color: #fff;
  width: 200px;
  padding: 20px;
  position: fixed; /* Fixed position to keep it static while scrolling */
  top:200px; /* Adjusted to start below the header */
  bottom: 60px; /* Adjusted to end above the footer */
  left: 0;
  z-index: 800; /* Lower than header and footer */
  overflow-y: auto; /* Enables scrolling within the leftbar if needed */
}

.leftbar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.leftbar li {
  margin-bottom: 10px;
}

.leftbar button {
  background-color: darkred;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.leftbar button:hover {
  background-color:  #ed115f;
}
