/* trainroute.css */

.train-route {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.train-route h2 {
  text-align: center;
  margin-bottom: 20px;
}

.train-route .input-container {
  display: flex;
  flex-direction: column; /* Changed to column direction */
  align-items: center; /* Center align items */
  margin-bottom: 20px;
}

.train-route .input-container input[type="text"] {
  padding: 8px 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px; /* Added margin-bottom */
}

.train-route .input-container button {
  padding: 8px 12px;
  font-size: 14px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}



.train-route .input-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.train-route .input-container input[type="text"],
.train-route .input-container button {
  padding: 8px 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.train-route .input-container button {
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.train-route .error {
  color: red;
  margin-top: 10px;
}

.train-route .table-container {
  margin-top: 20px;
}

.train-route table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.train-route table th,
.train-route table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.train-route table th {
  background-color: #f2f2f2;
}

.train-route table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}
