.footer {
  text-align: center;
  padding: 20px 0;
  background-color: #f0f0f0;
}

.footer p {
  display: flex;
  justify-content: space-between; /* Align content at opposite ends */
  align-items: center;
}

.left-content {
  display: flex;
  align-items: center;
}

.right-content {
  display: flex;
  align-items: center;
}

.footer-text {
  font-weight: bold;
  margin-right: 10px; /* Adjust margin as needed */
}

.footer-link {
  display: inline-block;
  padding: 10px 20px;
  text-decoration: none;
  color: #fff;
  background-color: red;
  border: 1px solid rebeccapurple;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.footer-link:hover {
  background-color: #ed115f;
  color: #fff;
}

.footer-link:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.4); /* Example of focus style */
}
